.main {
  padding: 10px;
  height: 100vh;
}
.layout {
  display: flex;
}
.sidebar {
  height:600px;
  width: 300px;
  border-radius: 5px;
  background-color: #0eae56;
  box-shadow: 0 0 2px gray;
  margin-right: 20px;
  color: white;
}
.content {
  width: 100%;
  height: 100%;
}
.header {
  height: 10vh;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color: white;
}
.body {
  height: auto;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color: white;
}
.logo h6 {
  font-size: 1.5rem;
  text-align: center;
  margin: 20px 0px;
}
.menu {
  margin-top: 50px;
  padding: 5px;
}
.menu-item {
  margin-top: 30px;
}
.menu-item a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}
.menu-item i {
  font-size: 1.2rem;
  margin: 0 15px;
}
/**To use active--we use useLocation hook**/
.active {
  background-color: white;
  padding: 10px;
}
.active a,
.active i {
  color: #02AD4F;
}

.header-content {
  margin: 0 15px;
  display: flex;
  align-items: center;
  height: 50px;
  justify-content: flex-end;
}
.header-content i {
  margin-right: 20px;
  font-size: 1.2rem;
}
.header-content a {
  color: black;
  text-decoration: none;
  margin: 0 10px;
  text-transform: uppercase;
  font-size: 1.2rem;
}
